* {
    margin: 0;
    padding: 0;
}

*, *:before, *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    background: #eee;
}

a {
    color: inherit;
    text-decoration: none;
}

.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

[class*="col-"] {
    float: left;
    padding: 10px;
}

.row::after {
    content: "";
    clear: both;
    display: table;
}

.box {
    background: #fff;
    -webkit-box-shadow: 0px 3px 3px rgba(0,0,0,0.15);
            box-shadow: 0px 3px 3px rgba(0,0,0,0.15);
    padding: 20px;
    position: relative;
}

.col--centered {
    margin: auto;
    float: none;
}

.float-left {
    float: left;
}

@media only screen and (max-width: 768px) {
    [class*="col-"] {
        width: 100%;
    }
}


